	export default {
		name: "",
		components: {

		},
		props: {},
		data() {
			return {
				carouselList: []
			}
		},
		methods: {
			getCarousel() {
				uni.showLoading()
				uni.request({
					url: "https://www.imovietrailer.com/superhero/index/carousel/list?qq=2684425594",
					method: 'POST',
					data: {

					},
					header: {
						'custom-header': 'application/x-www-form-urlencoded' //自定义请求头信息
					},
					success: res => {
					
						if (res.statusCode === 200) {
							this.carouselList = res.data.data
							
							
						}
					},
					fail: () => {},
					complete: () => {
						uni.hideLoading()
					}
				})
			}
		},
		mounted() {
           this.getCarousel()
		},
		onLoad() {
           this.getCarousel()
		},
		filters: {

		},
		computed: {

		},
		watch: {

		},
		directives: {

		}
	}
